.background2block {
    padding-top: 0;
    background: url("/img/background6.jpg") no-repeat center center fixed;
    background-size: 100%;
}

.main-info {
    margin-top: 4rem;
    margin-bottom: 10rem;
}

.img-info,
.block-info {
    width: 50%;
}

.container {
    margin-bottom: 1rem;
}

.names {
    padding: 1rem;

    letter-spacing: 0.3rem;
    font-family: 'Pattaya', sans-serif;
    font-size: 3.5rem;
}

.block-info {
    display: inline-block;
}

.block-names {
    display: flex;
    justify-content: flex-end;

    margin-bottom: 2rem;
}

.place-time {
    display: flex;
    justify-content: flex-end;
}

.place-time-text {
    font-family: 'Caveat';
    font-style: inherit;
    font-size: 2rem;
    font-weight: 700;
}

.separator-text {
    display: flex;
    justify-content: center;
}

.separator {
    width: 50vw;
}