
header {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 3rem;
  padding-top: 1rem;
  position: fixed;
  top: 0;
  width: 100%;

  -webkit-box-shadow: -1px 11px 7px -1px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: -1px 11px 7px -1px rgba(34, 60, 80, 0.2);
  box-shadow: -1px 11px 7px -1px rgba(34, 60, 80, 0.2);
  background-color: $gray;

  z-index: 10;
}

ul {
  display: flex;
  justify-content: center;
  align-items: center;

  &:hover li a{
    filter: blur(0.07rem);
  }

  &:hover li a:hover {
    font-size: 1.5rem;
    filter: blur(0rem);
  }
}

li {
  display: block;
  padding: 1.2rem;
  color: #fff;
  font-size: 1rem;
  font-family: fantasy;
  text-transform: uppercase;

  & a {
    font-family: 'Caveat', sans-serif;
    font-size: 1.2rem;
    text-decoration: none;
    color: #ebebe7;
    transition: .6s;
  }
}
