.wedding-info {
  margin-bottom: 2rem;
}

.wedding-info-up {
  display: flex;
  justify-content: center;
}

.wedding-info-up-article {
  font-family: 'Caveat';
  font-size: 3vmax;
  text-transform: uppercase;
  text-align: center;

  margin-bottom: 2vmax;
}

.wedding-info-text {
  display: flex;
  justify-content: center;
  margin-bottom: 2vmax;
}

.wedding-info-right,
.wedding-info-left {
  width: 50%;
}

.wedding-right-ask {
  padding-right: 2vmax;
  display: flex;
  justify-content: flex-end;

  font-family: 'Caveat';
  font-size: 2.5vmax;
}

.wedding-left-ask {
  padding-right: 2vmax;
  display: flex;

  font-family: 'Caveat';
  font-size: 2.5vmax;
}

.wedding-right-text {
  padding-right: 2vmax;
  display: flex;
  justify-content: flex-end;
  text-align: right;

  font-family: 'Caveat';
  font-size: 1.5vmax;
}

.wedding-left-text {
  display: flex;
  text-align: right;

  font-family: 'Caveat';
  font-size: 1.5vmax;
  text-align: left;
}

.wedding-info-left {
  padding-left: 2vmax;
  font-family: 'Caveat';

  border-style: none none none solid;
  border-width: 2px;
  border-color: #6A696B;
  border-radius: initial;
}

.wedding-info-down-article {
  display: flex;
  justify-content: center;
  padding-bottom: 0.5rem;

  font-family: 'Caveat';
  font-size: 2.5vmax;
}

.wedding-info-down-text {
  display: flex;
  justify-content: center;

  font-family: 'Caveat';
  font-size: 2vmax;
}