.way-info {
  margin-bottom: 1vmax;
  background: #c19a59;
  display: flex;
  justify-content: center;

  font-family: 'Roboto';
  font-size: 1.5vmax;
}

.way-article {
  padding-top: 2vmax;
  margin-bottom: 1vmax;
  display: flex;
  justify-content: center;

  font-family: 'Caveat';
  font-size: 2vmax;
  text-transform: uppercase;
  text-align: center;
}

.way-text {
  display: flex;
  justify-content: center;

  font-family: 'Roboto';
  font-size: 1.5vmax;

  margin-bottom: 2vmax;
}

.way-button {
  display: flex;
  justify-content: center;

  margin-bottom: 1vmax;
}

.custom-btn {
  padding: 10px 25px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  background: transparent;
  outline: none !important;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
}

.btn-4 {
  width: 21vmin;
  height: 4.5vmin;
  line-height: 42px;
  padding: 0;
  border: none;
}
.btn-4 span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.btn-4:before,
.btn-4:after {
  position: absolute;
  content: "";
  right: 0;
  top: 0;
  background: #000;
  transition: all 0.3s ease;
}
.btn-4:before {
  height: 50%;
  width: 2px;
}
.btn-4:after {
  width: 20%;
  height: 2px;
}
.btn-4:hover:before {
  height: 100%;
}
.btn-4:hover:after {
  width: 100%;
}
.btn-4 span:before,
.btn-4 span:after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  background: #000;
  transition: all 0.3s ease;
}
.btn-4 span:before {
  width: 2px;
  height: 50%;
}
.btn-4 span:after {
  width: 20%;
  height: 2px;
}
.btn-4 span:hover:before {
  height: 100%;
}
.btn-4 span:hover:after {
  width: 100%;
}

.link-map {
  text-decoration: none;
  color: black;

  &:before {
    text-decoration: none;
    color: black;
  }
  font-size: 1vmax;
}